(function() {
  'use strict';

  angular
    .module('olivejs')
    .directive('oliveNavbar', acmeNavbar);

  /** @ngInject */
  function acmeNavbar() {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/navbar/navbar.html',
      scope: {
          creationDate: '='
      },
      controller: NavbarController,
      controllerAs: 'vm',
      bindToController: true
    };

    return directive;

    /** @ngInject */
    function NavbarController(moment) {
      var vm = this;

      // "vm.creation" is avaible by directive option "bindToController: true"
      vm.relativeDate = moment(vm.creationDate).fromNow();
    }
  }

})();

(function() {
  'use strict';

  angular
    .module('olivejs')
    .directive('acmeMalarkey', acmeMalarkey);

  /** @ngInject */
  function acmeMalarkey(malarkey) {
    var directive = {
      restrict: 'E',
      scope: {
        extraValues: '='
      },
      template: '&nbsp;',
      link: linkFunc,
      controller: MalarkeyController,
      controllerAs: 'vm'
    };

    return directive;

    function linkFunc(scope, el, attr, vm) {
      var watcher;
      var typist = malarkey(el[0], {
        typeSpeed: 40,
        deleteSpeed: 40,
        pauseDelay: 800,
        loop: true,
        postfix: ' '
      });

      el.addClass('acme-malarkey');

      angular.forEach(scope.extraValues, function(value) {
        typist.type(value).pause().delete();
      });

      watcher = scope.$watch('vm.contributors', function() {
        angular.forEach(vm.contributors, function(contributor) {
          typist.type(contributor.login).pause().delete();
        });
      });

      scope.$on('$destroy', function () {
        watcher();
      });
    }

    /** @ngInject */
    function MalarkeyController($log, githubContributor) {
      var vm = this;

      vm.contributors = [];

      activate();

      function activate() {
        return getContributors().then(function() {
          $log.info('Activated Contributors View');
        });
      }

      function getContributors() {
        return githubContributor.getContributors(10).then(function(data) {
          vm.contributors = data;

          return vm.contributors;
        });
      }
    }

  }

})();

(function() {
  'use strict';

  angular
    .module('olivejs')
    .factory('githubContributor', githubContributor);

  /** @ngInject */
  function githubContributor($log, $http) {
    var apiHost = 'https://api.github.com/repos/Swiip/generator-gulp-angular';

    var service = {
      apiHost: apiHost,
      getContributors: getContributors
    };

    return service;

    function getContributors(limit) {
      if (!limit) {
        limit = 30;
      }

      return $http.get(apiHost + '/contributors?per_page=' + limit)
        .then(getContributorsComplete)
        .catch(getContributorsFailed);

      function getContributorsComplete(response) {
        return response.data;
      }

      function getContributorsFailed(error) {
        $log.error('XHR Failed for getContributors.\n' + angular.toJson(error.data, true));
      }
    }
  }
})();

(function() {
  'use strict';

  angular
    .module('olivejs')
    .service('AuthService', AuthService);

  /** @ngInject */
  function AuthService($http, $log, $resource,store, $q, jwtHelper, CORE_API_PATH) {


    this.getAccessToken = function(){
      return store.get('accessToken');
    }

    this.login = function(userName, password) {

      var deferred = $q.defer();

      generateRefreshToken(userName, password)
        .then(function(refreshToken) {
          store.set('refreshToken', refreshToken);

          $http({
            url: CORE_API_PATH + '/tokens/access',
            method: 'POST',
            headers: {
               'Authorization': refreshToken
            }
          }).then(function(response) {

            store.set('accessToken', response.data.token);
            var tokenPayload = jwtHelper.decodeToken(response.data.token);
            deferred.resolve(true);

          }, function(error) {
            $log.error(error);
            deferred.reject(error);
          });

      })
      .catch(function(err) {
          deferred.reject(err);
      });

      return deferred.promise;
    }



    function generateRefreshToken(userName, password){
      var defered = $q.defer();
      var promise = defered.promise;

      $http.post(CORE_API_PATH + '/tokens/refresh', {
          'username': userName,
          'password': password
        }
      )
        .success(function(data) {
          var tokenPayload = jwtHelper.decodeToken(data.token);
          defered.resolve(data.token);
        })
        .error(function(err) {
          defered.reject(err)
        });

      return promise;
    }
  }
})();

